/* Table of contents */

/*
 *  1. Global
 *  2. Colors
 *  3. Typography
 *  4. Space
 *  5. Media Query
 *  6. Border
 *  7. Misc
 *
 */

/* 1. Global */
$base: 7px;
$type-scale: 1.25;
$line-height-base: 1.5;
$row-width: 1000px;

/* 2. Colors */
$emperor: #333;
$white: #fff;
$white-2: #fdfdfd;
$silver: #999;
// $mercury: #e7e5e5;
$alto: #dbdbdb;
$boulder: #757575;

$jade: #1dd069;
$jade-lighten: #03bd85;

$blue-jeans: #2eafe5;
$red-orange: #ff3b30;
$android-green: #8cc152;
$jaffa: #f07736;
$tan-hide: #f7945f;
$alabaster: #f7f7f7;
$black: #000;
$drover: #f8fda8;

$color-primary: $jade;
$color-secondary: $emperor;
$color-info: $blue-jeans;
$color-success: $android-green;
$color-warning: $jaffa;
$color-danger: $red-orange;

$body-bg: $alabaster;
$body-color: $emperor;
$link-color: $color-primary;
$muted: $silver;

$color-facebook: #3b5998;
$color-google-plus: #dd4b39;

/* 3. Typography */
$font-sans-serif: "Noto Sans", sans-serif;
// $font-serif: Bitter, serif;
$font-special: Damion, cursive; /* This is optional, remove if nessesary */

$font-family-body: $font-sans-serif;
// $font-family-heading: $font-serif;

$font-size-base: ($base * 2);
$font-size-h1: ($font-size-base * $type-scale * $type-scale * $type-scale * $type-scale);
$font-size-h2: ($font-size-base * $type-scale * $type-scale * $type-scale);
$font-size-h3: ($font-size-base * $type-scale * $type-scale);
$font-size-h4: ($font-size-base * $type-scale);
$font-size-h5: $font-size-base;
$font-size-h6: ($font-size-base / $type-scale);

$font-weight-normal: 400;
$font-weight-bold: 700;

/* 4. Space */
$space-xs: ($base / 2);
$space-sm: ($base);
$space-md: ($base * 2);
$space-lg: ($base * 4);
$space-xlg: ($base * 8);
$space-xxlg: ($base * 16);

/* 5. Media Query */
@custom-media --small-only (max-width: 639px);
@custom-media --medium-up (min-width: 640px);
@custom-media --large-up (min-width: 1024px);
@custom-media --responsive-table (max-width: 991px); // copy bootstrap 4
@custom-media --card-3-breakpoint (min-width: 820px);

/* 6. Border */
$radius: 3px;
$radius-big: ($base * 1.5);
$border-color: $alto;

/* 7. Misc */
$opacity: 0.6;

// nature z-index = 0
// z-index-10 = 10
// z-index-20 = 20
// z-index-max = 100
$z-index-10: 10;
$z-index-20: 20;
$z-index-30: 30;
$z-index-max: 100;

$btn-group-dropdown-menu: 160px;

$transition-time: 0.25s;
$transition-effect: ease-in;
